@import "components";

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #3b8c75;
  padding: 0.5em;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  .logo {
    color: #bdf2e3;
    font-size: 1.2rem;
    font-weight: 700;
  }

  .menu {
    padding: 0.2em;
    cursor: pointer;

    i {
      font-size: 1.4rem;
      color: #bdf2e3;
    }
  }
}

.sidebar {
  border: 1px solid #63a693;
  width: 50%;
  margin-left: auto;
  background-color: #3b8c75;
  color: #fff;
  position: fixed;
  right: 0;
}

.sidebar-default {
  list-style-type: none;
  padding-left: 0;
  margin-top: 0.5em;
}

.sb-item {
  padding: 0.2em 0.5em;
  cursor: pointer;

  &:hover {
    background-color: #316c5b;
  }
}

.sidebar-projects {
  .sb-p-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-right: 1.5em;
    //background-color: #316c5b;
    border: 1px solid #63a693;
    cursor: pointer;

    &:hover {
      background-color: #9ad9c7;
      color: #316c5b;
    }

    h3 {
      margin: 0;
      padding: 0.2em 0.5em;
    }
  }

  .new-project {
    display: none;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin: 0.5em 0.2em;

    input {
      border: none;
      padding: 0.2em;
      width: 80%;
      background-color: #bdf2e3;

      &:focus {
        outline: none;
      }

    }

    i {
      color: #bdf2e3;

      &:hover {
        color: #fff;
      }
    }
  }

  .sb-p-items {
    list-style-type: none;
    padding-left: 0;
  }
}

main {
  margin: 0.5em;

  .category {
    font-weight: 700;
    color: #316c5b;
    border-bottom: 1px solid #63a693;
    padding: 0.5em 0;
  }
}

.td-list {
  list-style-type: none;
  padding-left: 0;

  &-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0.5em 0;
    border-bottom: 1px solid #9ad9c7;

    .info {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;

      .status {
        color: green;
        margin-right: 0.2em;
      }

      .pr-h {
        color: red;
      }

      .pr-m {
        color: blue;
      }

      .pr-l {
        color: goldenrod;
      }

      .st-f {
        text-decoration: line-through;
        color: gray;
      }

      .due-dt {
        margin-left: 1.2em;
      }
    }

    .btns {
      span {
        padding: 0.2em;
      }

      .view {
        color: #316c5b;
      }

      .edit {
        color: goldenrod;
      }

      .delete {
        color: red;
      }
    }
  }
}
